@font-face {
  font-family: "CoFoSans";
  src: url("../fonts/../fonts/CoFoSans-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
body {
  font-family: "CoFoSans";
  font-size: 16px;
  font-weight: 400;
  color: #8c94a8;
  line-height: 1.5;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 1350px;
  padding: 0 15px;
  margin: 0 auto;
}

.page {
  flex: 1 1 auto;
  background-color: #f4f6fb;
  padding-top: 164px;
}

._blue {
  background-color: #007bfb !important;
  color: #fff !important;
}

.header {
  background-color: #fff;
  border-radius: 0 0 32px 32px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.08);
  position: absolute;
  left: 0;
  right: 0;
  height: 164px;
}

h1 {
  font-size: 40px;
  line-height: 36px;
  font-weight: 500;
  color: #2a2a30;
  padding: 20px 0 30px;
}

.nav {
  position: relative;
  display: flex;
  padding-bottom: 30px;
}
.nav__list {
  display: flex;
}
@media (max-width: 420px) {
  .nav__list {
    display: none;
  }
  .nav__list.menu--active {
    position: absolute;
    top: 50px;
    left: -15px;
    display: inline-block;
    background-color: #fff;
    width: 150px;
    padding: 10px 0 10px;
    border-radius: 0 32px 32px 32px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.08);
  }
  .nav__list.menu--active a {
    margin-bottom: 10px;
    margin-left: 15px;
  }
}
.nav a {
  font-size: 12px;
  font-weight: 500;
  color: #282d3c;
  padding: 0 18px;
  height: 48px;
  width: 60px;
  border-radius: 28px;
  background-color: #f4f6fb;
  margin-right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.burger {
  display: none;
}
@media (max-width: 420px) {
  .burger {
    display: block;
    position: absolute;
    top: 7px;
    left: 76px;
    background-color: inherit;
    border: none;
    padding: 0;
    width: 50px;
    height: 30px;
    cursor: pointer;
  }
  .burger::before, .burger::after {
    content: "";
    position: absolute;
    left: 0;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #007bfb;
  }
  .burger::before {
    left: 10px;
  }
  .burger::after {
    left: 30px;
  }
  .burger__line {
    position: absolute;
    left: 20px;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #007bfb;
  }
}

.posts {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 0;
  margin-right: -3%;
}
.post-card {
  flex: 0 1 47%;
  margin-right: 3%;
  margin-bottom: 3%;
  background-color: #fff;
  border-radius: 28px;
  padding: 20px 50px 20px 20px;
  min-height: 340px;
}
@media (max-width: 767px) {
  .post-card {
    flex: 0 1 100%;
    padding-right: 20px;
    min-height: auto;
  }
}
.post-card__inner {
  height: 100%;
  max-width: 384px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .post-card__inner {
    max-width: 100%;
  }
}
.post-card__title {
  font-size: 32px;
  line-height: 36px;
  font-weight: 500;
  color: #282d3c;
  padding-bottom: 30px;
}
@media (max-width: 1350px) {
  .post-card__title {
    font-size: calc(20px + 12 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .post-card__title {
    line-height: calc(24px + 12 * (100vw - 320px) / 1030);
  }
}
.post-card__text {
  flex: 1 1 auto;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .post-card__text {
    max-width: 384px;
  }
}